                      <li data-aos="fade-up">
                        <h3>什麼是終止懷孕？</h3>
                        <p>終止懷孕又可稱為墮胎、人工流產、終止妊娠。你可以透過手術或口服藥物的方式，將胎兒從子宮內清除。隨著懷孕時間的長短不同會有不同的執行療程和影響，以及法律規範。若透過合法管道進行醫療流程，會有醫師給予適時的檢查與手術，可以降低風險。反之，若沒有依循合法正常的途徑或太晚執行終止懷孕，很有可能會對身體留下永久的傷害。</p>
